@import "https://fonts.googleapis.com/css?family=Open+Sans:wght@100;300;400;700display=swap";
body {
  font-family: Open + Sans, sans-serif;
  color: #333;
  height: 100vh;
  font-size: clamp(1rem, 2vw, 1.2rem);
}

label {
  color: #151f23ba;
}

button#submit-button {
  border: none;
}

.card-body {
  padding: 1rem;
}

div#response {
  background: #fff;
  height: fit-content;
  margin-bottom: 10px;
}

.container {
  padding: 1em;
}

h1 {
  font-family: Helvetica, sans-serif;
  font-weight: 700;
}

.chat-message {
  color: #fff;
  background-color: #0b93f6;
  border-bottom: 1px solid #000;
  border-radius: 18px;
  margin-bottom: 10px;
  padding: 1em;
}

@media (width <= 768px) {
  .container {
    padding: .5em;
  }
}
